import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasAdministradoraMHO } from '../../../../../dependencies/administradoraMHODependency';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { FLUXO_CAIXA_VALUES, TODAS } from '../../../../../models/enumerated/mho/statusContaPagarMHOEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, FluxoCaixaMHORelRV, REPORT_EVENT, REPORT_TITLE } from './fluxoCaixaMHORelRV';

const DEFAULT_VALUES: FluxoCaixaMHORelRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  gerarCabecalhoPadraoAdministradora: true,
  status: TODAS.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const defaultWidth: number = 110;

const FluxoCaixaMHOReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const { handleSubmit, ...formProps } = useForm<FluxoCaixaMHORelRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Fluxo de Caixa"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasAdministradoraMHO(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={2977}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <InputGroupInline
          id="referencia"
          label="Referência"
          labelWidth={defaultWidth}
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { required: true } }}
          placeholder="Informe a Referência"
          type={InputType.REFERENCE}
        />

        <InputGroupInline
          id="status"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Status"
          labelWidth={defaultWidth}
          inputDropDownProps={{
            optionsProvider: FLUXO_CAIXA_VALUES,
            selectProps: { autoFocus: true, isClearable: false },
          }}
          isPrependLabel={true}
          placeholder="Informe o status"
          type={InputType.DROP_DOWN}
        />

        <Row className="mb-3">
          <Col>
            <CustomInput
              type="switch"
              id="isContaPagarPaginaDiferente"
              name="isContaPagarPaginaDiferente"
              label="Imprimir contas a pagar em uma nova página"
              innerRef={formProps.register}
            />
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <CustomInput type="switch" id="imprimirCaixa" name="imprimirCaixa" label="Imprimir em regime de caixa" innerRef={formProps.register} />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default FluxoCaixaMHOReportView;
