import { GlobalParameter } from '../models/dtos/ger/globalParameter';

export function hasAdministradoraMHO(globalParameter: GlobalParameter): string | undefined {
  let result = null;
  if (!globalParameter.administradora.administradoraMHO) {
    result =
      'A administradora deve ser configurada no módulo de Honorários. Para configurar acesse:\n' +
      'Módulo Honorários > Configurações > Administradora';
  }

  return result;
}
